import React from 'react';
import InputNumber from './InputNumber';
const inputNumberStyle = {
    margin: '0 5px',
};
const FromEvery = ({ value, onChange, front, middle, back, fromMin = 0, fromMax = 59, everyMin = 1, everyMax = 59, disabled, }) => {
    const splits = (value || '').split('/');
    const from = parseInt(splits[0], 10);
    const every = parseInt(splits[1], 10);
    const notifyChange = (fromStr, everyStr) => {
        const s = `${fromStr}/${everyStr}`;
        if (onChange)
            onChange(s);
    };
    const handleFromChange = (v) => {
        notifyChange(`${v}`, `${every}`);
    };
    const handleEveryChange = (v) => {
        notifyChange(`${from}`, `${v}`);
    };
    return (<span>
      {front}
      <InputNumber disabled={disabled} min={fromMin} max={fromMax} value={from} style={Object.assign({}, inputNumberStyle)} onChange={handleFromChange}/>
      {middle}
      <InputNumber disabled={disabled} min={everyMin} max={everyMax} value={every} style={Object.assign({}, inputNumberStyle)} onChange={handleEveryChange}/>
      {back}
    </span>);
};
export default FromEvery;
